.Logo {
  background-color: white;
  padding: 8px;
  box-sizing: border-box;
  border-radius: 5px;
  height: 100%;
  width: 100%;
}

.Logo img {
  height: 100%;
  width: 100%;
}

@media (max-width: 380px) {
  .Logo img {
      height: 90%;
      width: 90%
  }
}

@media (max-width: 330px) {
  .Logo img {
      width: 80%;
  }
}

.Header {
  /* border: 2px solid black; */
  display: grid;
  grid-template-columns: 375px auto 50px;
  border-bottom: 2px solid #ccc;
  background-color: white;
}

.Main {
  /* border: 2px solid green; */
  grid-column: 1;
}

.MobileOnly {
  /* border: 2px solid pink; */
}

.Icon {
  /* border: 2px solid red; */
}

.Navigation {
  /* border: 2px solid yellow; */
  grid-column: 2;
  margin: auto 0;
}

.Navigation ul {
  display: grid;
  text-align: right;
  grid-template-columns: repeat(5, 1fr);
  float: right;
  /* border: 2px solid gray; */
  padding: 0;
  height: 100%;
}

.Navigation li {
  list-style: none;
  text-align: center;
  -webkit-align-self: center;
          align-self: center;
  height: 100%;
  /* border: 2px solid purple; */
  padding: 0 5px;
}

.Control {
  /* border: 2px solid blue; */
  grid-column: 3;
}

@media (min-width: 1000px) {
  .MobileOnly {
      display: none;
  }
} 

@media (max-width: 999px) {
  .Navigation {
    display: none;
  }

  .Control {
    display: none;
  }

  .Header {
    grid-template-columns: repeat(1, 1fr);
  }

  .Main {
    grid-template-columns: auto 350px auto;
    display: grid;
  }

  .MobileOnly {
    grid: 1;
  }

  .Icon {
    grid: 2;
  }
}

