.ToggleIcon {
  width: 35px;
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: column;
          flex-flow: column;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-items: center;
          align-items: center;
  padding: 35px 0;
  box-sizing: border-box;
  cursor: pointer;
}

.ToggleIcon div {
  width: 90%;
  height: 3px;
  background-color: black;
}

.NavItems a{
  display: grid;
  width: 100%;
  box-sizing: border-box;
  height: 35px;
  -webkit-align-content: center;
          align-content: center;
  text-decoration: none;
  color: black;
  font-weight: 700;
}

.NavItems a:hover,
.NavItems a:active,
.NavItems a.active {
  color: rgb(107, 81, 158);
  border-bottom: 2px solid rgb(107, 81, 158);
  /* border-bottom: 3px solid blue; */
  /* color: rgb(42, 73, 152);
  background-color: #c6c6c6;
  border-bottom: 3px solid rgb(108, 114, 122); */
}

@media (max-width: 999px) {

  .NavItems li {
    width: 100%;
  }

  .NavItems a {
    height: 100%;
    text-align: center;
    text-align: left;
    padding-left: 10px;
  }

  .NavItems a:hover,
  .NavItems a:active,
  .NavItems a.active {
    color: rgb(107, 81, 158);
    border-bottom: none;
  }
}
@media (max-width: 999px) {
  .BackDrop {
    width: 100%;
    height: 100%;
    position: fixed;
    z-index: 100;
    left: 0;
    top: 0;
    background-color: rgba(0, 0, 0, 0.5);
  }
}
.SideDrawer {
  height: 100%;
  background: white;
  box-shadow: 1px 0px 7px rgba(0, 0, 0, 0.5);
  position: fixed;
  top: 0;
  left: 0;
  width: 70%;
  max-width: 400px;
  z-index: 200;
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
  transition: -webkit-transform 0.3s ease-out;
  transition: transform 0.3s ease-out;
  transition: transform 0.3s ease-out, -webkit-transform 0.3s ease-out;
  padding: 32px 16px;
  box-sizing: border-box;
}

.SideDrawer ul {
  padding: 0;
  margin: auto;
}

.SideDrawer li {
  list-style: none;
  text-align: center;
  height: 50px;
}

.SideFooter {
  /* border: 2px solid red; */
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  background-color: #171717;
  height: 15%;
  text-align: center;
  color: white;
}

.SideFooter > div {
  position: absolute;
  font-size: 11px;
  width: 95%;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  display: grid;
  grid-template-areas:    "phone fax"
                          "email email"
                          "address address";
  grid-column-gap: 20px;
  grid-row-gap: 5px;
}

.SideFooter a {
  color: white;
  text-decoration: none;
}

.Phone {
  grid-area: phone;
}

.Fax {
  grid-area: fax;
}

.Email {
  grid-area: email
}

.Address {
  grid-area: address;
}


@media (min-width: 1000px) {
  .SideDrawer {
    display: none;
  }

}

.SideDrawer.Open {
  -webkit-transform: translateX(0);
          transform: translateX(0);
}

.Home {
  width: 100%;
}

.Content {
  width: 500px;
  margin: auto;
  text-align: center;
}

@media (max-width: 540px) {
  .Content {
    width: auto;
  } 
}

.Home > img {
  width: 100%;
  object-fit: cover;
  height: 300px;
}
