.Dot {
  position: relative;
}

.Dot > img {
  position: absolute;
  bottom: 0;
  left: 50%;
  -webkit-transform: translate(-50%, 0);
          transform: translate(-50%, 0);
}

.HoverOn {
  -webkit-transform: scale(1.5);
          transform: scale(1.5);
}

.HoverOff {
  -webkit-transform: scale(1.2);
          transform: scale(1.2)
}

.Direction{
  height: 45vh;
  width: 95%;
  margin: auto;
}

.Direction > p {
  text-align: center;
}

.Direction > p > a {
  text-decoration: none;
  color: black;
}

.Testimonial {
  width: 100%;
}

.Testimonial > img {
  width: 100%;
  object-fit: cover;
  height: 300px;
}

.TestimonialContent {
  display: grid;
  grid-template-columns: 1fr;
  width: 500px;
  margin: auto;
  grid-gap: 2em;
  padding: 40px 20px;
}

@media (max-width: 600px) {
  .TestimonialContent {
    width: auto;
  }
}

.TestimonialContent > p {
  border-radius: 10px 10px 10px 10px;
  -moz-border-radius: 10px 10px 10px 10px;
  -webkit-border-radius: 10px 10px 10px 10px; 
  border: 1.5px solid rgb(107, 81, 158);
  padding: 20px;
  box-shadow: 0px 0px 15px 2px rgba(0,0,0,0.75);
}
.Contact {
